<template>
<!--  -->
<div class="selectComponents">
  <div class="content-box-title">
    
  </div>
  <div class="content-box">
    <el-row :gutter="20">
      <el-col :span="12">
        <el-form :label-position="labelPosition" label-width="120px" :model="formLabelAlign">
          <el-form-item label="">
            <el-input v-model="formLabelAlign.name" placeholder="SRC001"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="value" placeholder="">
              <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="12">

      </el-col>
    </el-row>
    <div class="btn-down">
      <el-button type="primary"></el-button>
      <el-button> </el-button>
    </div>

    <el-row :gutter="0">
      <el-col :span="8">
        <div class="header-excl">
          POS
        </div>
        <excl0></excl0>
        <div class="black"></div>
      </el-col>
      <el-col :span="8">
        <div class="header-excl">
          
        </div>
        <excl1></excl1>
        <div class="black"></div>
      </el-col>
      <el-col :span="8">
        <div class="header-excl">
          
        </div>
        <excl2></excl2>
        <div class="black"></div>
      </el-col>
    </el-row>
  </div>



</div>
<!--  -->
</template>

<script>
import excl0 from '~/components/excl0.vue'
import excl1 from '~/components/excl1.vue'
import excl2 from '~/components/excl2.vue'
import paging from '~/components/paging.vue'

export default {
  components: {
    paging,
    excl0,
    excl1,
    excl2
  },
  data() {
    return {
      labelPosition: 'right',
      formLabelAlign: {
        name: '',
        region: '',
        type: ''
      },
      options: [{
        value: '1',
        label: 'pos'
      }, {
        value: '2',
        label: ''
      }, {
        value: '3',
        label: ''
      }, {
        value: '4',
        label: ''
      }, {
        value: '5',
        label: ''
      }],
      value: '2'
    }
  }
}
</script>

<style scoped>
h1 {
  text-align: center;
  padding-top: 100px;
  font-weight: bold;
  font-size: 30px;
  letter-spacing: 1px;
}

.btn-down {
  margin: 15px 0 30px 0;
}

.content-box-title {
  background: #20A0FF;
  color: #fff;
  padding: 15px;
  border-radius: 3px 3px 0 0;
}

.header-excl{
  background: #1D8CE0;
  padding: 15px;
  text-align: center;
  color:#fff;
  letter-spacing: 1px;
}

.black{
  height:30px;
}

.content-box {
  background: #fff;
  padding: 15px;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.14);
  border-radius: 0 0 3px 3px;
}
</style>



