<template>
<!--  -->
<div class="leftMenu" :class="[this.logo ? true : 'active', '']">
  <el-menu default-active="1-4-1" class="el-menu-vertical-demo" :collapse="!this.logo" theme="dark" :router="true" :unique-opened="true" >
    <el-submenu index="1">
      <template slot="title">
      <i class="el-icon-upload"></i>
      <span slot="title"></span>
    </template>
      <el-menu-item index="1-1"></el-menu-item>
      <el-menu-item index="1-2"></el-menu-item>
      <el-menu-item index="1-3"></el-menu-item>
      <el-menu-item index="1-4"></el-menu-item>
    </el-submenu>
    <el-submenu index="2">
      <template slot="title">
      <i class="el-icon-edit"></i>
      <span slot="title"></span>
    </template>
      <el-menu-item index="2-1"></el-menu-item>
      <el-menu-item index="2-2"></el-menu-item>
      <el-menu-item index="2-3"></el-menu-item>
      <el-menu-item index="2-4"></el-menu-item>
    </el-submenu>
    <el-submenu index="3">
      <template slot="title">
      <i class="el-icon-setting"></i>
      <span slot="title"></span>
    </template>
      <el-menu-item index="3-1"></el-menu-item>
      <el-menu-item index="3-2"></el-menu-item>
      <el-menu-item index="3-3"></el-menu-item>
      <el-menu-item index="3-4"></el-menu-item>
    </el-submenu>
    <el-submenu index="4">
      <template slot="title">
      <i class="el-icon-star-on"></i>
      <span slot="title"></span>
    </template>
      <el-menu-item index="4-1"></el-menu-item>
      <el-menu-item index="4-2"></el-menu-item>
      <el-menu-item index="4-3"></el-menu-item>
      <el-menu-item index="4-4"></el-menu-item>
    </el-submenu>
    <el-submenu index="5">
      <template slot="title">
      <i class="el-icon-time"></i>
      <span slot="title"></span>
    </template>
      <el-menu-item index="5-1"></el-menu-item>
      <el-menu-item index="5-2"></el-menu-item>
      <el-menu-item index="5-3"></el-menu-item>
      <el-menu-item index="5-4"></el-menu-item>
    </el-submenu>
    <el-submenu index="6">
      <template slot="title">
      <i class="el-icon-share"></i>
      <span slot="title"></span>
    </template>
      <el-menu-item index="6-1"></el-menu-item>
      <el-menu-item index="6-2"></el-menu-item>
      <el-menu-item index="6-3"></el-menu-item>
      <el-menu-item index="6-4"></el-menu-item>
    </el-submenu>
  </el-menu>
</div>
<!--  -->
</template>

<script>
import {
  mapGetters
} from 'vuex'

export default {
  components: {},
  data() {
    return {}
  },
  methods: {

  },
  created() {
    console.log(this.logo);
  },
  computed: {
    ...mapGetters([
      'logo'
    ])
  },
  watch: {
    change() {
      return this.logo
    },
  },
  directives: {},
  mounted() {}
}
</script>

<style scoped lang="scss">
.leftMenu {
    width: 200px;
    transition: 0.5s;
    height:100%;
    &.active {
        width: 64px;
    }
}
</style>



